import React from 'react';
function isLogin(nextState, replace) {
    // console.log(nextState);
    // replace('/')
}
const routerConfig = [{
    path: '/',
    onEnter: isLogin,
    getComponent(nextState, cb) {
        require.ensure([], (require) => {
            cb(null, require('./routes/index/home_layout').default)
        }, 'home_layoutJs')
    },
    indexRoute: {
        getComponent(nextState, cb) {
            require.ensure([], (require) => {
                cb(null, require('./routes/index/home').default)
            }, 'homeJs')
        },
    },
    childRoutes: [
        {
            path: '/mine',
            getComponent(nextState, cb) {
                require.ensure([], (require) => {
                    cb(null, require('./routes/mine/mine').default)
                }, 'mineJs')
            },
        },
    ]
}, {
    path: '/520',
    getComponent(nextState, cb) {
        require.ensure([], (require) => {
            cb(null, require('./routes/520/Five20').default)
        }, '520Js')
    },
}, {
    path: '/goodsDetail',
    getComponent(nextState, cb) {
        require.ensure([], (require) => {
            cb(null, require('./routes/goodsDetail/goodsDetail').default)
        }, 'goodsDetailJs')
    },
},{
    path: '/management',
    getComponent(nextState, cb) {
        require.ensure([], (require) => {
            cb(null, require('./routes/management/management').default)
        }, 'managementJs')
    },
},{
    path: '/oilCoupons',
    getComponent(nextState, cb) {
        require.ensure([], (require)=>{
            cb(null, require('./routes/oilCoupons/oilCoupons').default)
        }, 'oilCoupons')
    }
},{
    path: '*',
    getComponent(nextState, cb){
        require.ensure([], (require) => {
            cb(null, require('./routes/not_found').default)
        }, "notFound")
    }
}];

export default routerConfig;


